
.animated-enter-active {
  -webkit-animation: travel 4s ease-in;
          animation: travel 4s ease-in;
}
.animated-leave-active {
  -webkit-animation: fade-spin 2s linear;
          animation: fade-spin 2s linear;
}
@-webkit-keyframes travel {
0% {
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
    opacity: 0;
}
20% {
    -webkit-transform: translateX(-50px) translateY(0);
            transform: translateX(-50px) translateY(0);
}
30% {
    -webkit-transform: translateX(-50px) translateY(50px);
            transform: translateX(-50px) translateY(50px);
    opacity: 1;
}
40% {
    -webkit-transform: translateX(-60px) translateY(-50px);
            transform: translateX(-60px) translateY(-50px);
}
50% {
    -webkit-transform: translateX(60px) translateY(-50px);
            transform: translateX(60px) translateY(-50px);
}
60% {
    -webkit-transform: translateX(50px) translateY(-80px);
            transform: translateX(50px) translateY(-80px);
}
80% {
    -webkit-transform: translateX(30px) translateY(0);
            transform: translateX(30px) translateY(0);
}
90% {
    -webkit-transform: translateX(-80px) translateY(0);
            transform: translateX(-80px) translateY(0);
}
100% {
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
}
}
@keyframes travel {
0% {
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
    opacity: 0;
}
20% {
    -webkit-transform: translateX(-50px) translateY(0);
            transform: translateX(-50px) translateY(0);
}
30% {
    -webkit-transform: translateX(-50px) translateY(50px);
            transform: translateX(-50px) translateY(50px);
    opacity: 1;
}
40% {
    -webkit-transform: translateX(-60px) translateY(-50px);
            transform: translateX(-60px) translateY(-50px);
}
50% {
    -webkit-transform: translateX(60px) translateY(-50px);
            transform: translateX(60px) translateY(-50px);
}
60% {
    -webkit-transform: translateX(50px) translateY(-80px);
            transform: translateX(50px) translateY(-80px);
}
80% {
    -webkit-transform: translateX(30px) translateY(0);
            transform: translateX(30px) translateY(0);
}
90% {
    -webkit-transform: translateX(-80px) translateY(0);
            transform: translateX(-80px) translateY(0);
}
100% {
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
}
}
@-webkit-keyframes fade-spin {
100% {
    -webkit-transform: rotate(360deg) scale(4);
            transform: rotate(360deg) scale(4);
    opacity: 0;
}
}
@keyframes fade-spin {
100% {
    -webkit-transform: rotate(360deg) scale(4);
            transform: rotate(360deg) scale(4);
    opacity: 0;
}
}



