
.magnifier {
  border: 1px solid black;
}
.blue {
  fill: rgba(0, 113, 199, 0.5);
  stroke: rgba(0, 84, 148, 0.5);
}
.blue2 {
  fill: rgba(0, 84, 148, 0.5);
  stroke: rgba(0, 55, 97, 0.5);
}
.blue3 {
  fill: rgba(0, 142, 250, 0.5);
  stroke: rgba(0, 55, 97, 0.5);
}
.blue4 {
  fill: rgba(46, 164, 255, 0.5);
  stroke: rgba(0, 55, 97, 0.5);
}
.slide-enter-active, .slide-leave-active {
  transition: all 0.5s ease-out;
}
.slide-enter, .slide-leave-to {
  opacity: 0;
}
.slide-enter {
  -webkit-transform: translateX(-20px);
          transform: translateX(-20px);
}
.slide-leave-to {
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
}



